import { BrowserRouter as Router, Routes, Route, Navigate } from "react-router-dom";
import Header from "./Components/Header";
import Footer from "./Components/Footer";
import Home from "./Pages/Home";
import About from "./Pages/About";
import Contact from "./Pages/Contact";
import Projects from "./Pages/Projects";
import Technologies from "./Pages/Technologies";
import React, { useState, useEffect } from "react";
import md5 from "md5";

const Login = () => {
  const [isVerified, setIsVerified] = useState(false);
  const checkPw = (e) => {
    e.preventDefault();

    const answer = document.getElementById("password").value;

    // Super unsafe, but isn't like I keep nuclear codes here
    if (md5(answer) === "3bc34554307d76e3faabe31a2a0fd352") {
      setIsVerified(true);
      sessionStorage.setItem("isVerified", 'true');
    } else {
      alert("Sorry, that's not it");
    }
  };

  return (
    <>
      {sessionStorage.getItem("isVerified") === 'true' || isVerified ? <App />
        :
        (
          <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2">
            <form onSubmit={checkPw} className="bg-gray-700 shadow-md rounded px-8 pt-6 pb-8 mb-4">
              <label className="block text-gray-100 text-sm font-bold mb-2">
                Password
              </label>
              <div className="mb-4">
                <input id="password" name="password" className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 leading-tight focus:outline-none focus:shadow-outline" />
              </div>
              <button className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline">Sign in</button>
            </form>
          </div>
        )
      }
    </>
  );
}

function addDarkClass(isDark) {
  if (isDark) {
    document.documentElement.classList.add('dark')
  } else {
    document.documentElement.classList.remove('dark')
  }
}

function App() {
  const [darkMode, setDarkMode] = useState(true);

  useEffect(() => {
    let isDark = localStorage.getItem("isDark") === null ? true : localStorage.getItem("isDark") === "true";
    addDarkClass(isDark);
    setDarkMode(isDark);
  }, [])

  const toggleDarkmode = () => {
    let isDark = localStorage.getItem("isDark") !== "true";
    localStorage.setItem("isDark", isDark);
    addDarkClass(isDark);
    setDarkMode(isDark);
  };

  return (
    <Router>
      <Header toggleDarkMode={toggleDarkmode} isDark={darkMode} />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route path="/about" element={<About />} />
        <Route path="/contact" element={<Contact />} />
        <Route path="/projects" element={<Projects />} />
        <Route path="/technologies" element={<Technologies />} />
        <Route path="*" element={<Navigate to="/" />} />
      </Routes>
      <Footer />
    </Router>
  );
}
export default Login;
